.thumb {
	position: relative;
	display: inline-flex;
	border-radius: 2px;
	border: 1px solid #eaeaea;
	margin-bottom: 8px;
	margin-right: 8px;
	width: 85px;
	height: 85px;
	padding: 4px;
	box-sizing: border-box;
}

.thumbInner {
	display: flex;
	min-width: 0;
	overflow: hidden;
}

img {
	display: block;
	width: 90px;
	height: 100%;
	object-fit: cover;
}

.remove {
	position: absolute;
	right: -5px;
	top: -5px;
	background: #eb3c30;
	border-radius: 50%;
	color: #fff;
	border: none;
	font-size: 20px;
	width: 20px;
	height: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
}
