.form {
	margin-top: 30px;
}

.title {
	margin: 0;
	color: var(--color-white);
	font-size: 3rem;
	font-weight: 400;
	opacity: 0.7;
}
.inputbox {
	display: flex;
	align-items: center;
	margin-top: 35px;
}
.input {
	width: calc(100% - 45px);
	background: none;
	border: none;
	font-size: 2rem;
	color: var(--color-white);
	text-align: center;
	border-bottom: 2px solid var(--color-white);
}
.button {
	background: none;
	border: none;
	color: var(--color-white);
	font-size: 2rem;
}
