.settingBox .setting {
	display: none;
}
.settingBox.active .setting {
	display: block;
}

.setBtn {
	position: absolute;
	left: 2vw;
	bottom: 2vh;
	border: none;
	background: none;
	font-size: 2.2rem;
	cursor: pointer;
	color: var(--color-white);
}
.setting {
	position: absolute;
	left: 2vw;
	bottom: 8vh;
	height: 80vh;
	width: 500px;
	background-color: var(--color-white);
	border-radius: 5px;
	padding: 20px;
}
.settingTitle {
	margin: 0;
}
